.wrapper
.question-body,
.action {
    margin: 2rem 0;
}

.question-body {
    padding: 1rem;
    height: 27rem;
    position: relative;
}

.action {
    width: 100%;
    height: 26rem;
    font-size: 1.2rem;
}

.more {
    position: absolute;
    width: 90%;
    top: 22.5rem;
}

.q-text {
    margin: 1rem 0;
    font-size: 1.15rem;
}

.answers {
    font-weight: bold;
    color: #164029;
}

.close-btn {
    background-color: rgba(0,0,0,0);
    margin: 0;
    padding: 5px;
    float: right;
}

@media screen and (max-width: 980px) {
    .question-body {
        height: 35.5rem;
    }

    .more {
        top: 31.5rem;
        width: 85%;
    }

    .action {
        height: 35.5rem;
    }
}

@media screen and (max-width: 640px) {
    .question-body {
        height: 22rem;
    }

    .more {
        width: 95%;
        top: 18rem;
    }

    .action {
        height: 22rem;
    }
}

@media screen and (max-width: 320px) {
    .question-body {
        height: 29rem;
    }

    .more {
        width: 90%;
        top: 25rem;
    }

    .close-btn {
        color: white;
        background-color: rgba(0,0,0,0);
        margin: 1.3rem .5rem 0 .5rem;
        padding: 0;
        font-size: 2rem;
    }    
}