.navBackground {
    background-color: #BAE8C8;
}
.navBackground {
    background-color: #BAE8C8;
}
footer {
    background-color: #BAE8C8;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
}
.link {
    color: #202528;
    margin: 1.2rem 2rem;
    float: right;
}

.link:hover {
    color: #3CB371;
    font-size: bold;
    text-decoration: none;
}
.btn {
    background-color: #BAE8C8;
    border: none;
    color: #202528;
}
h2,
p,
.btn {
    margin: 2rem;
}

.display-3 {
    font-style: italic;
}
.card-container {
    margin: 3rem 0;
}

.btn {
    margin: .8rem 0;
}

@media screen and (max-width: 640px) {
    .card-container {
        width: 90%;
        margin: 3rem auto;
    }
}
.about-container {
    margin: 3rem 0;
}

.item {
    margin: 1rem 0;
}
.source {
    color: #202528;
    font-style: italic;
}

.source:hover {
    color: #202528;
    text-decoration: none;
    font-weight: bold;
}
@media screen and (max-width: 640px) {
    .modal-content {
        position: fixed;
        padding: 0;
        margin: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: none;
        border-radius: 0;
        background-color: rgba(102, 102, 102, .95);
        color: white;
        font-size: 1.5rem;
        overflow-y: scroll;
    }
}
.wrapper
.question-body,
.action {
    margin: 2rem 0;
}

.question-body {
    padding: 1rem;
    height: 27rem;
    position: relative;
}

.action {
    width: 100%;
    height: 26rem;
    font-size: 1.2rem;
}

.more {
    position: absolute;
    width: 90%;
    top: 22.5rem;
}

.q-text {
    margin: 1rem 0;
    font-size: 1.15rem;
}

.answers {
    font-weight: bold;
    color: #164029;
}

.close-btn {
    background-color: rgba(0,0,0,0);
    margin: 0;
    padding: 5px;
    float: right;
}

@media screen and (max-width: 980px) {
    .question-body {
        height: 35.5rem;
    }

    .more {
        top: 31.5rem;
        width: 85%;
    }

    .action {
        height: 35.5rem;
    }
}

@media screen and (max-width: 640px) {
    .question-body {
        height: 22rem;
    }

    .more {
        width: 95%;
        top: 18rem;
    }

    .action {
        height: 22rem;
    }
}

@media screen and (max-width: 320px) {
    .question-body {
        height: 29rem;
    }

    .more {
        width: 90%;
        top: 25rem;
    }

    .close-btn {
        color: white;
        background-color: rgba(0,0,0,0);
        margin: 1.3rem .5rem 0 .5rem;
        padding: 0;
        font-size: 2rem;
    }    
}
.action-card {
    margin: 1.5rem auto;
    border: solid 1px #164029;
    width: 90%;
}

.action-count {
    font-size: 2rem;
}

.suggestion {
    font-size: 1.5rem;
    margin: 0 .5rem;
    padding: 0;
}

.learn-more {
    background-color: transparent;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.learn-more:hover {
    background-color: transparent;
    color: #212529;
    font-weight: bold;
    text-transform: uppercase;
}

.close-btn {
    background-color: rgba(0,0,0,0);
    margin: 0;
    padding: 5px;
    float: right;
}

@media screen and (max-width: 640px) {
    .close-btn {
        color: white;
        background-color: rgba(0,0,0,0);
        margin: 2.2rem .5rem 0 .5rem;
        padding: 0;
        font-size: 2rem;
    }    
}
.welcome {
    margin: 2rem auto;
    font-style: italic;
}

@media screen and (max-width: 640px) {
    .welcome {
        font-size: 2.5rem;
    }

    .user-body {
        margin-bottom: 6rem;
    }
}
